.toll-list-item-container,
.toll-list-label-container {
    width: 100%;
}

.toll-list-item-container *,
.toll-list-label-container * {
    display: inline-block;
    text-align: center;
}

.toll-list-label,
.toll-list-item {
    width: 15%;
}

.toll-list-label-container {
    background: #222;
    position: fixed;
    box-shadow: 0px 1px 2px rgba(0,0,0,0.4);
    top: 170px;
}

.toll-list-items {
    margin-top: 82px;
    height: 730px;
    overflow: auto;
}

.toll-list-label h6 {
    margin: 40px 0px 10px;
}

.toll-list-item-container {
    background: #222;
    box-shadow: 0px 1px 2px rgba(0,0,0,0.6);
}

.toll-list-container {
    background: #1a1a1a;
}

.toll-list-stops-container {
    width: 40%;
    min-width: 460px;
    display: inline-block;
    margin-top: 5px;
}

.toll-list-stop h6 {
    position: relative;
    display: inline-block;
    margin: 0px 10px;
    width: 90px;
}

.toll-list-stop {
    margin-left: 50px;
    margin-bottom: 5px;
}

.stops-btn {
    box-shadow: none;
    background: #222;
    color: white;
    border: none;
}

.stops-btn:hover {
    cursor: pointer;
}

.route-info-container {
    width: 55%;
    position: relative;
    bottom: 50px;
    display: inline-flex;
    float: right;
}

.show-routes-btn {
    margin-left: 80px;
}

.route-info h4 {
    margin: 10px 0px;
    display: inline;
    margin-left: 40px;
}

.toll-popup-container {
    position: fixed;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    z-index: 5000;
}

.toll-popup-background {
    height: 100%;
    width: 100%;
    background: rgba(0,0,0,0.5);
}

.toll-popup-card {
    position: fixed;
    width: 600px;
    top: 15%;
    left: 50%;
    margin-left: -300px;
    background: #333;
    border-radius: 10px;
    box-shadow: 0px 2px 3px black;
}

.toll-popup-card h1 {
    text-align: center;
    color: white;
}

.toll-popup-routes {
    width: 90%;
    margin: 10px auto;
    box-shadow: 0px 0px 0px 1px #666;
    border-radius: 6px;
    padding: 10px;
}

.toll-popup-route-info h5,
.toll-popup-route-info h6 {
    color: white;
    margin: 0px;
}

.loader-container {
    position: fixed;
    height: 100px;
    width: 100px;
    top: 10px;
    left: 50%;
    margin-left: -50px;
}

.loader-container svg {
    z-index: 9999;
    width: 100%;
}

.loader-container svg * {
    stroke: white;
}

.exit-btn-container {
    margin: auto;
    padding: 20px 0px 20px;
    width: 80px;
}

.exit-btn {
    width: 100%;
    font-size: 18px;
    padding: 12px 0px;
    background: #4f4f4f;
    border: none;
    border-radius: 6px;
    box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.3);
    color: white;
}

.exit-btn:hover {
    cursor: pointer;
    background: #555;
}

.api-btn,
.show-routes-btn {
    background: #333;
    color: white;
    padding: 12px 24px;
    border: none;
    border-radius: 6px;
    box-shadow: 0px 2px 4px rgba(0,0,0,0.3);
}

.api-btn:disabled,
.show-routes-btn:disabled {
    box-shadow: 0px 0px 0px 1px #444;
    background: none;
    color: #444;
}

.api-btn:hover,
.show-routes-btn:hover {
    background: #444;
}

button:hover {
    cursor: pointer;
}

button:disabled:hover {
    background: none;
    cursor: default;
}

.hidden {
    display: none;
}

.search-input {
    background: none;
    color: white;
    margin-top: 30px;
    padding: 8px 6px;
    font-size: 18px;
    border-radius: 6px;
    border: none;
    box-shadow: 0px 0px 0px 1px #666;
}

@media (max-width: 1043px) {
    .route-info-container {
        top: -63px;
    }
}