.stops-csv-reader-container {
    height: 180px;
    color: white;
    width: 100%;
}

.stops-csv-reader-container h1,
.stops-csv-reader-container p {
    margin-left: 20px;
}

.set-rate-container {
    position: fixed;
    top: 400px;
    left: 280px;
    animation-name: fade-in;
    animation-duration: 600ms;
}

@keyframes fade-in {
    0%      {opacity: 0%;}
    100%    {opacity: 100%;}
}