.input-container h5 {
    display: inline-block;
}

.input-container input {
    margin: 0px 10px;
    width: 80px;
    text-align: center;
}

.stop-adjust-container {
    margin-top: 20px;
    position: fixed;
    width: 100%;
    background: #222;
    left: 0;
    animation-name: example;
    animation-duration: 0.75s;
    animation-fill-mode: forwards;
    animation-timing-function: ease-out;
}

@keyframes example {
    0%  {top: 1000px;}
    100%  {top: 0px;}
}

.stop-adjust-container h1,
.stop-adjust-container p {
    margin: 0px 10px;
}

a {
    padding: 5px 10px 7px;
    background: rgb(61, 145, 86);
    text-decoration: none;
    border-radius: 6px;
    color: white;
    box-shadow: 1px 2px 4px rgba(0, 0, 0, 0.4);
}

a:hover {
    cursor: pointer;
    background: rgb(71, 160, 98);
}

.hidden {
    display: none;
}

.check-in-container {
    margin-left: 20px;
}

.check-in-container input {
    display: block;
    font-size: 16px;
    padding: 4px 6px;
    background: #222;
    border: none;
    color: white;
    box-shadow: 0px 0px 0px 1px white;
    border-radius: 6px;
    padding: 6px 10px;
}

.check-in-container label {
    font-size: 12px;
}

.check-in-form p {
    display: inline-block;
}

.afr-toggle {
    width: 300px;
    color: white;
    border: none;
    border-radius: 6px;
    padding: 10px;
    margin-bottom: 30px;
    box-shadow: 2px 3px 3px rgba(0, 0, 0, 0.3);
}

.afr-toggle:hover {
    cursor: pointer;
    opacity: 0.85;
}

.afr-true {
    background: #008000;
}

.afr-false {
    background: #9e0000;
}

.input-error {
    border: 2px solid #9e0000;
    border-radius: 5px;
}

.input-good {
    border: 2px solid #008000;
    border-radius: 5px;
}



/* Remove Arrows from Number Input */
/* Chrome, Safari, Edge, Opera */
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

/* Firefox */
input[type=number] {
  -moz-appearance: textfield;
}

.download-btn {
    margin-left: 20px;
}