.background-overlay {
    position: fixed;
    top: 0px;
    left: 0px;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.4);
}

.popup-container {
    position: relative;
    z-index: 9999;
    margin: 200px auto;
    padding: 40px 20px;
    background: #333;
    color: white;
    width: 90%;
    max-width: 1000px;
    border-radius: 10px;
    box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.3);
    height: 400px;
    overflow-y: scroll;
}

.popup-container p {
    display: inline-block;
    margin: 0px 5px;
    text-align: left;
}

.incr-container {
    position: fixed;
    text-align: center;
    margin: auto;
    top: 700px;
    width: 90%;
    max-width: 1000px;
}

.incr-btn {
    display: inline-block;
    padding: 16px 30px;
    border-radius: 150px;
    font-size: 48px;
    color: white;
    border: none;
    margin: 20px;
    box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.3);
}

.incr-btn:hover {
    opacity: 0.8;
    cursor: pointer;
}

.incr-container h4 {
    position: relative;
    display: inline-block;
    bottom: 10px;
    color: white;
    font-size: 24px;
    text-shadow: 0px 2px 4px rgba(0, 0, 0, 0.5);
}

.up-btn {
    background:rgb(61, 145, 86);
}

.dn-btn {
    background: rgb(156, 87, 122);
}

.up-btn:disabled,
.dn-btn:disabled {
    background: #999;
    opacity: 0.2;
}

.up-btn:disabled:hover,
.dn-btn:disabled:hover {
    cursor: default;
}


.list-container {
    text-align: center;
    border-radius: 10px;
    max-width: 800px;
    margin: 0px auto 0px 40px;
    padding: 12px 24px;
    transition: 200ms;
    color: #333;
}

.list-container:hover {
    cursor: pointer;
}

.finish-list {
    border-radius: 10px;
    text-align: center;
    max-width: 800px;
    margin: 0px auto;
    padding: 12px 24px;
    transition: 200ms;
    color: #333;
}

.finish-list:hover {
    cursor: pointer;
}

.selected {
    background: rgb(55, 129, 77);
    color: white;
    box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.3);
    transition: 200ms;
}

.selected:hover {
    background: rgb(61, 145, 86);
    color: white;
}

.unselected {
    box-shadow: 0px 0px 0px 1px rgb(146, 71, 71);
    transition: 200ms;
    color: white;
}

.unselected:hover {
    background: #444;
}

.finish-btn {
    position: relative;
    text-align: center;
    text-decoration: none;
    font-size: 28px;
    color: white;
    background: rgb(61, 145, 86);
    border: none;
    border-radius: 10px;
    box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.3);
    padding: 12px 50px;
}

.finish-btn:hover {
    cursor: pointer;
    background: rgb(71, 160, 98);
}

.finish-lists-container {
    margin-top: 40px;
}

.finish-btn-container {
    position: fixed;
    z-index: 9999;
    width: 90%;
    max-width: 1000px;
    height: 50px;
    background: #333;
    top: 200px;
}

.view-btn {
    position: relative;
    bottom: 63px;
    left: 910px;
    width: 20px;
    background: none;
    padding: 0px;
    border: none;
}

.view-btn:hover {
    cursor: pointer;
    opacity: 0.8;
}

.finish-view-btn {
    position: relative;
    bottom: 55px;
    left: 930px;
}

.view-background {
    position: fixed;
    top: 0px;
    left: 0px;
    width: 100%;
    height: 100%;
    z-index: 10000;
    background: rgba(0, 0, 0, 0.6);
}

.view-window {
    position: relative;
    z-index: 10001;
    width: 90%;
    max-width: 600px;
    height: 600px;
    background: #333;
    margin: 160px auto;
    border-radius: 10px;
    pointer-events: auto;
}

.view-window h2 {
    padding: 50px 0px 30px;
    font-size: 24px;
    text-align: center;
}

.view-window input {
    margin: 10px auto;
    padding: 6px 4px;
    width: 100%;
    font-size: 18px;
    border-radius: 6px;
    border: 1px solid #555;
    background: #444;
    display: block;
    color: white;
}

.view-window-btn-container {
    margin: auto;
    width: 50%;
}

.view-window-btn {
    margin: auto;
    width: 48%;
    font-size: 18px;
    padding: 12px 0px;
    background: #4f4f4f;
    border: none;
    border-radius: 6px;
    box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.3);
    color: white;
}

.view-window-btn:hover {
    cursor: pointer;
    background: #555;
}

.view-input-container {
    margin: auto;
    width: 80%;
    max-width: 400px;
}

.full-address {
    border: 1px solid #ccc;
}

.progress-bar-back {
    position: fixed;
    bottom: 40px;
    border-radius: 100px;
    height: 10px;
    background: #444;
    width: 90%;
    max-width: 1000px;
}

.progress-bar {
    position: fixed;
    bottom: 40px;
    border-radius: 100px;
    height: 10px;
    background: rgb(71, 160, 98);
    transition: 200ms;
}

.phone {
    position: relative;
}

.view-input-container a {
    text-decoration: none;
    position: relative;
    bottom: 40px;
    left: 370px;
    background: none;
    box-shadow: none;
}

.view-input-container a:hover {
    background: none;
    box-shadow: none;
}

.view-input-container a img {
    height: 25px;
    width: 25px;
}

.sel-container {
    position: fixed;
    text-align: center;
    width: 90%;
    bottom: 80px;
    max-width: 1000px;
}

.sel-btn {
    position: relative;
    z-index: 15000;
    font-size: 20px;
    padding: 10px 48px;
    border-radius: 150px;
    border: none;
    background: rgb(61, 145, 86);
    color: white;
    box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.3);
}

.sel-btn:hover {
    cursor: pointer;
    opacity: 0.8;
}

.loader-container {
    text-align: center;
}